:root {
  --darkElements: hsl(209, 23%, 22%);
  --darkBackground: hsl(207, 26%, 17%);
  --lightElements: hsl(200, 15%, 8%);
  --lightText: hsl(200, 15%, 8%);
  --lightBackground: hsl(0, 0%, 98%);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 16px;
  color: var(--lightText);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3, h4, p {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
.flex-column {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.flex-row {
  display: flex;
  align-items: center;
}
h2 {
  font-size: 14px;
}
.App {
  min-height: 100vh;
  background-color: var(--lightBackground);
  color: yellow;
}
main {
  flex-direction: row;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.Header {
  height: 10vh;
  justify-content: space-between;
  align-items: center;
  padding: 0 5vw 0 5vw;
}
header * {
  margin: 0;
  padding: 0;
}
header div {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50%;
}
header div:hover {
  cursor: pointer;
}
.theme-icon {
  height: 50%;
  margin-right: 5px;
}
.filters {
  display: flex;
  margin: 20px 0 10px 0;
  justify-content: space-between;
  padding: 0 10vmin 0 10vmin;
  height: 40px;
}

.filters * {
  padding-left: 10px;
  padding-right: 10px;
  box-sizing: border-box;
  border: none;
  outline: none;
  height: 40px;
  border-radius: 5px;
  box-shadow: -1px 2px 9px 1px rgba(32, 32, 32, 0.75);
  -webkit-box-shadow: -1px 2px 9px 1px rgba(182, 180, 180, 0.75);
  -moz-box-shadow: -1px 2px 9px 1px rgba(172, 170, 170, 0.75);
}
.search {
  width: 25vw;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.search-input {
  background: none;
  box-shadow: none;
  color: inherit; 
}
.search img {
  padding: 0;
  box-shadow: none;
  height: 40%;
  margin: 0;
}

.Details button {
  color: inherit;
}
select {
  width: 15vw;
}

.Card {
  
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 15vw;
  height: 250px;
  margin: 20px;
  background: white;
  border-radius: 6px;
  transition: 0.1s;
  box-shadow: -1px 2px 9px 1px rgba(136, 134, 134, 0.75);
  -webkit-box-shadow: -1px 2px 9px 1px rgba(151, 148, 148, 0.75);
  -moz-box-shadow: -1px 2px 9px 1px rgba(121, 120, 120, 0.75);
  animation: myAnim 1s ease 0s 1 normal forwards;
}
.Card:active {
  animation: myAnimOut 1s ease 0s 1 normal forwards;
}
@keyframes myAnim {
	0% {
		opacity: 0;
		transform: rotateX(-100deg);
		transform-origin: top;
	}

	100% {
		opacity: 1;
		transform: rotateX(0deg);
		transform-origin: top;
	}
}
@keyframes myAnimOut {
	0% {
		opacity: 1;
		transform: rotateX(0deg);
		transform-origin: top;
	}

	100% {
		opacity: 0;
		transform: rotateX(70deg);
		transform-origin: top;
	}
}

.card-text {
  margin: 8px;
  display: flex;
  min-height: 40%;
  max-height: 40%;
  flex-direction: column;
  align-items: flex-start;
  font-size: 12px;
  justify-content: space-around;
}


.Card > img {
  max-height: 50%;
  min-height: 50%;
  max-width: 100%;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  background-color: rgb(14, 13, 13);
}
.Details {
  margin: auto;
  padding: auto;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: inherit;
}
.Details h2 {
  font-size: 25px;
  line-height: 1;
}
article {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 70vw;
  height: 60vh;
}
.imgAndLink {  
   width: 50%;
   display: flex;
   flex-direction: column;
   justify-content: space-around;
   height: 100%;
}
.details-img {
    width: 100%;
    max-height: 80%;
}
.details-text {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: space-evenly;
  height: 100%;
  width: 45%;
}

.details-middle {
  font-size: 14px;
} 

.details-middle {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.borderCountries {
  margin: 5px;
}
button:hover {
  box-shadow: none;
  box-shadow: -1px 2px 9px 1px rgba(141, 140, 140, 0.75);
}
button {
  background: none;
  border: none;
  border-radius: 5px;
  padding: 0;
  padding: 5px 25px;
  box-shadow: -1px 2px 9px 1px rgba(216, 214, 214, 0.75);
  transition: 0.3s;
}
.spinner {
  display: absolute;
  top: 20;
  margin: auto;
  box-shadow: none;
  position: absolute;
  z-index: 20;
 color:rgb(138, 130, 130);
}
.climbSpinner {
  background-color: bisque;
}
.back {
  padding: 10px 30px;
  cursor: pointer;
}

@media (max-width: 768px) {
  main {
    gap: 0px;
    flex-wrap: wrap;
    flex-direction: row;
    padding: 0;
  }
  .Card {
    width: 60vw;
    margin: 0;
    margin-bottom: 20px;
  }
  
 .Details {
   flex-direction: column;
 }
 article {
   flex-direction: column;
   justify-content: space-between;
   align-items: center;
   height: 80vh;
   width: 85vw;
 }
 
 .imgAndLink {
   width: 100%;
   height: 60%;
   justify-content: space-around;
 }
 .details-text {
   width: 100%;
 }
 .details-middle {
   flex-direction: column;
   gap: 20px;
   align-items: flex-start;
 }
 .filters {
   flex-direction: column;
   height: 100px;
   margin-bottom: 20px;
 }
 .search {
   width: 80vw;
 }
 select {
   width: 50vw;
 }
 .Header {
   font-size: 10px;
 }
}
